import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { Button } from 'react-bootstrap';
import ProjectModal from './project-modal';

const ProjectCard = props => {
  const { cca2: code2 = '', title = null, info = {}  } = props.project || {};
  
  const [modalShow, setModalShow] = React.useState(false);
  const img_link = 'url('+info.imglink+')'

  return (
    <div className="project" style={{backgroundImage: img_link}}>
    <div className="desc">
      <div className="con">
        <h3><a>{title}</a>
          <span> { info.date }</span>
        </h3>
        <span> { info.content } </span>
        <h5> <a href={info.link}><i className={info.icon}/> {info.ref}</a> </h5>
        <p className="icon">
          {info.types.map((value, index) => {
            return <span><a>{value}</a></span>
          })}
        </p>
      </div>
      {/* <Button variant="primary" onClick={() => setModalShow(true)}>
        Read More
      </Button>
      <ProjectModal
        style={{opacity:1}}
        show={modalShow}
        onHide={() => setModalShow(false)}
      /> */}
    </div>
    </div>
  )
}

ProjectCard.propTypes = {
  country: PropTypes.shape({
    cca2: PropTypes.string,
    title: PropTypes.string.isRequired,
    info: PropTypes.shape({
      types: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired
    }).isRequired
  }).isRequired
};

export default ProjectCard;
