import React, { Component } from 'react'
import { Image } from 'react-bootstrap';
import PropTypes from 'prop-types';

const ExperienceCard = props => {
  const { cca2: code2 = '', title = null, info = {}  } = props.experience || {};

  return (
        <div className="row experience">
          <div className="col-sm-5 col-md-5 logo"> <Image className="icon col-lg-auto" src={info.link} rounded height="64"/> </div>
          <div className="col-sm-7 col-md-7 text">
            <div className="t2">{ info.org }</div>
            <div className="t1">{ title } </div>
            <div className="t3">{ info.time } | {info.place}</div>
          </div>
        </div>
  )
}

ExperienceCard.propTypes = {
  country: PropTypes.shape({
    cca2: PropTypes.string.isRequired,
    region: PropTypes.string.isRequired,
    name: PropTypes.shape({
      common: PropTypes.string.isRequired
    }).isRequired
  }).isRequired
};

export default ExperienceCard;
